.logo
  line-height: 0
  &--footer
    margin-bottom: 30px

  &__img
    max-width: 200px
    height: 64px
    &--footer
      max-width: 261px
      height: 84px

  &__img-mini
    max-width: 61px
    height: 64px
    display: none

.menu
  display: flex
  &--footer
    display: flex
    flex-direction: column
  @media screen and ( max-width: 820px )
    display: none

  &__item
    margin-right: 40px
    position: relative
    font-family: "Montserrat-SemiBold", sans-serif
    font-weight: 600
    &::after
      position: absolute
      content: ""
      border-top: 1px solid $kelp
      width: 0
      left: 50%
      top: 105%
      transform: translateX(-50%)
      transition: width .4s
    &:last-child
      margin-right: 0
    &:hover
      &::after
        width: 100%
    &--footer
      margin-right: 0
      margin-bottom: 20px
      &:last-child
        margin-bottom: 0
      &::after
        display: none

.title
  margin: 0
  margin-bottom: 60px
  font-family: "Montserrat-ExtraBold", sans-serif
  font-weight: 700
  font-size: 40px
  line-height: 50px
  text-transform: uppercase

.text
  margin: 0

.button
  padding: 8px 83px
  font-family: "Montserrat-ExtraBold", sans-serif
  font-weight: 700
  font-size: 20px
  line-height: 24px
  color: $white
  background-color: $dingley
  border: 2px solid $dingley
  border-radius: 40px
  transition: background-color .4s
  &:hover
    background-color: transparent
    color: $dingley

.inner-icon
  display: flex
  align-items: center
  justify-content: center
  width: 40px
  height: 40px
  background-color: $dingley
  border-radius: 100px
  transition: transform .4s
  &:hover
    transform: scale(0.90)

.social-icon
  width: 24px
  height: 24px
  fill: $white

.input
  all: unset
  margin-bottom: 20px
  padding: 20px 15px
  border: 1px solid $silver
  border-radius: 10px
  width: 100%
  box-sizing: border-box
  cursor: pointer
